:root {
    --primary: rgb(159, 102, 102);
    --secondary: rgb(73, 73, 127);
    --dark: black;
    --light: #eee;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'fira sans',sans-serif;
}

body {
    background-color: var(--light);
}

.App {
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    padding: 16px;
}

.calculator {
    width: 100%;
    max-width: 400px;
    background-color: #eee;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
    padding: 16px;
    text-align: right;
    background-color: var(--dark);
    color: var(--light);
    font-size: 24px;
    font-weight: 300;
}

button {
    appearance: none;
    border: none;
    outline: none;
    color: #fff;
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    transition: 0.4;
}

button:hover{
    opacity: 0.9;
}

.operators {
  display: flex;  
}

.operators button {
    flex: 1 1 0%;
    background-color: var(--primary);
    font-weight: 700;  
}

.digits  {
    display: flex;
    flex-wrap: wrap;
}

.digits button {
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
}